  xl: '1200px',
  xxl: '1600px',
}
*/
.wide-screen{
  max-width: 1200px;
  margin: 0 auto;
  padding: 4px 8px;
}
.body-post {
  white-space: pre-wrap!important;
}
.ant-select-dropdown.dropdown-container .ant-avatar{
  width: 50px!important;
  height: 50px!important;
}
.ant-select-dropdown.dropdown-container .ant-card-meta-title{
    padding: 10px 0px!important;
}
.ant-select-dropdown.dropdown-container  .ant-avatar img{
   object-fit: cover!important;  
}
