/* Html */
html { height: auto!important; overflow:auto!important; }
.mydrawer .ant-drawer-close{
  top: calc(50% - 28px)!important;
}
.button.ant-btn.login-form {
  width: 100%!important;
}

.mydrawer .ant-drawer-title{
  font-family: 'Jost' !important;
}
.mydrawer .ant-drawer-header {
  padding: 4px 16px;
  background-color: var(--color-gray-light);
}
.mydrawer .ant-drawer-title h1{
  font-size: 24px!important;
  font-weight: bold !important;
}
.mydrawer .btn-add-new{
  margin:4px 20px!important;
  max-width: 50%!important; 
}
.mydrawer .empty-text h2{
  font-size: 24px;
}
.mydrawer .ant-drawer-body {
  min-height: 80vh!important;
  justify-content: stretch!important;
}
.ant-drawer .drawer-title svg{
  color: var(--color-primary);
  font-size: 24px!important;
  margin-right: 8px;
}
.ant-drawer .drawer-title {
  display: flex;
  justify-content: start;
  align-items: center;
  cursor: pointer;
}
.mydrawer .ant-drawer-title h1:hover{
  color: var(--color-primary)!important;
}
.ant-drawer.empty .ant-drawer-body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh!important;
} 

