:root {
  /* paleta de colores */
  --color1: #A734FF;
  --color2-1: #ff3b90;
  --color2: #D7A3FF;
  --color3: #ffc358;
  --color4: #3bbfb8;
  --color5: #47dcff;
  --color6: #98ebff;

  /* Colores principales */
  --color-gray: #5f5f5f;
  --color-pink-light:#F3E5FE;
  --color-gray-dark: #444444;
  --color-gray-light: #f9f2ff;
  --color-white: #fff;
  --color-primary: var(--color1); 
  --color-secundary: var(--color2);
  --color-yellow: var(--color3);
  --color-green: var(--color4);
  --color-blue-ligth: var(--color6);
  --color-blue-ligth-1: var(--color5);
  --color-blue-dark-1: var(--color2);
}
